*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container{
    width: 100%;
}

.container .wrapper {
    max-width: 1200px;
    margin: auto;
}

.container .wrapper form {
    display: flex;
    justify-content: space-between;
}

.container .wrapper form input {
    width: 75%;
    height: 50px;
    font-size: 30px;
    border: none;
    outline: none;
    border-bottom: 2px solid grey;
    border-radius: 10px;
}

.container .wrapper form button {
    width: 15%;
    height: 50px;
    border: none;
    border-radius: 10px;
}